import React, { useState, useEffect, useRef, useMemo } from "react";

import Select2 from "react-select2-wrapper";
import SectionHeader from "../../../../components/Form/SectionHeader";
import SVG from "svg.js";
import { convertToDecimal } from "helpers/UtilsHelper";
import { FormGroup, Row, Col, Button, ButtonGroup } from "reactstrap";

export const bookingRoomModes = {
  LIST: "list",
  MAP: "map",
};

export const ETHERA_VENUE = 1;
export const NON_ETHERA = 2;

const BookingVenue = ({
  venueType,
  venues,
  venueId,
  spaces,
  spaceId,
  changeVenueType,
  onChangeRoom,
  onChangeLocation,
  currentRoomMode,
  onChangeMode,
  modalVenue,
}) => {
  const locationData = useMemo(
    () => venues.map((venue) => ({ ...venue, text: venue.name })),
    [venues]
  );
  const spaceIds = useMemo(() => spaces.map((s) => s.id), [spaces]);

  return (
    <div className="venue mb-4">
      <SectionHeader icon="far fa-building" title="Location" />
      <Row>
        <Col>
          <ButtonGroup className="mt-2">
            <Button
              color="primary"
              type="button"
              onClick={() => changeVenueType(ETHERA_VENUE)}
              active={venueType === ETHERA_VENUE}
            >
              <i className="fa fa-circle"></i>Ethera
            </Button>
            <Button
              color="primary"
              type="button"
              onClick={() => changeVenueType(NON_ETHERA)}
              active={venueType === NON_ETHERA}
            >
              <i className="fa fa-circle"></i>Other
            </Button>
          </ButtonGroup>
        </Col>
      </Row>
      {venueType === ETHERA_VENUE && (
        <Row>
          <Col md={12} className="mt-2">
            <Row>
              <Col md={6}>
                <FormGroup className="mb-0">
                  <label
                    className="form-control-label"
                    htmlFor="location-selector"
                  >
                    Location
                  </label>
                  <Select2
                    id="location-selector"
                    className="form-control"
                    defaultValue="0"
                    options={{
                      placeholder: "Select",
                      data: locationData,
                    }}
                    value={venueId}
                    onChange={(e) => onChangeLocation(e.target.value)}
                  />
                </FormGroup>
              </Col>
            </Row>
            <Row className="row-no-gutters">
              <Col md={12}>
                <FormGroup className="mb-0">
                  <Row>
                    <Col md={6}>
                      <label
                        className="form-control-label"
                        htmlFor="room-selector"
                      >
                        Room
                      </label>
                    </Col>
                  </Row>
                  <Row>
                    <Col md={12}>
                      <ButtonGroup className="mt-1 mb-1">
                        <Button
                          color="primary"
                          onClick={() => onChangeMode(bookingRoomModes.LIST)}
                          active={currentRoomMode === bookingRoomModes.LIST}
                        >
                          <i className="fa fa-circle"></i>Room list
                        </Button>
                        <Button
                          color="primary"
                          onClick={() => onChangeMode(bookingRoomModes.MAP)}
                          active={currentRoomMode === bookingRoomModes.MAP}
                        >
                          <i className="fa fa-circle"></i>Room map
                        </Button>
                      </ButtonGroup>
                    </Col>
                  </Row>
                  <Row>
                    {currentRoomMode === bookingRoomModes.LIST ? (
                      <Col md={6}>
                        <RoomList
                          spaceId={spaceId}
                          spaces={spaces}
                          onChangeRoom={onChangeRoom}
                        />
                      </Col>
                    ) : (
                      <Col md={12} id="roommap-wrapper">
                        {venueId && (
                          <RoomMap
                            venue={modalVenue}
                            spaces={spaceIds}
                            spaceId={spaceId}
                            onChangeRoom={onChangeRoom}
                            wrapperId="roommap-wrapper"
                          />
                        )}
                      </Col>
                    )}
                  </Row>
                </FormGroup>
              </Col>
            </Row>
          </Col>
        </Row>
      )}
    </div>
  );
};

function RoomList(props) {
  const { spaceId, spaces, onChangeRoom } = props;
  return (
    <Select2
      id="room-selector"
      className="form-control"
      defaultValue="0"
      options={{
        placeholder: "Select",
        data: spaces,
      }}
      value={spaceId}
      onChange={(e) => onChangeRoom(e.target.value)}
    />
  );
}

function useLoadImage(url) {
  const [img, setImg] = useState(null);
  const [loadError, setLoadError] = useState(false);
  useEffect(() => {
    let imgObj = new Image();
    imgObj.onload = () => setImg(imgObj);
    imgObj.onerror = () => setLoadError(true);
    imgObj.src = url;
  }, [url]);
  return [img, loadError];
}

function RoomMap(props) {
  const { venue, wrapperId, onChangeRoom, spaceId, spaces } = props;
  const id = props.id || "room-map-drawing";
  const [draw, setDraw] = useState(null);
  const [img, imgLoadError] = useLoadImage(venue.roomMap);
  const [parentWidth, setParentWidth] = useState(500);
  const [parentHeight, setParentHeight] = useState(100);
  const svgItem = useRef();

  useEffect(() => {
    if (img && svgItem.current) {
      const { width, height } = img;
      const roomMapWrapper = document.getElementById(wrapperId);
      const parentWidth = roomMapWrapper ? roomMapWrapper.clientWidth : 500;
      const parentHeight = convertToDecimal((height / width) * parentWidth, 4);
      svgItem.current.innerHTML = "";
      setDraw(SVG(svgItem.current.id).size(parentWidth, parentHeight));
      setParentWidth(parentWidth);
      setParentHeight(parentHeight);
    }
  }, [img, svgItem]);

  useEffect(() => {
    const drawRooms = () => {
      const { width, height } = img;
      const weightWidth = parentWidth / width;
      const weightHeight = parentHeight / height;
      const parsedMap = JSON.parse(venue.roomMapMapping);
      draw.clear();
      draw.image(venue.roomMap, parentWidth, parentHeight);
      if (!(parsedMap instanceof Array)) {
        return;
      }

      for (let item of parsedMap) {
        const roomAvailable = spaces.indexOf(item.id) >= 0;
        let pointArr, childItem;
        childItem = draw.link(`#`);
        childItem.click((e) => {
          e.preventDefault();
          if (roomAvailable) {
            onChangeRoom(item.id);
          }
        });
        if (!roomAvailable) {
          childItem.node.style = "cursor: not-allowed;";
        }
        const listPoint = item.coords;
        switch (item.shape) {
          case "poly":
            pointArr = [];
            for (let i = 0; i < listPoint.length; i += 2) {
              const pointData = [
                listPoint[i] * weightWidth,
                listPoint[i + 1] * weightHeight,
              ];
              pointArr.push(pointData);
            }
            childItem.polyline(pointArr);
            break;
          case "rect":
            const x = (listPoint[2] - listPoint[0]) * weightWidth;
            const y = (listPoint[3] - listPoint[1]) * weightHeight;
            childItem.rect(x, y).attr({
              x: listPoint[0] * weightWidth,
              y: listPoint[1] * weightHeight,
            });
            break;
          default:
            break;
        }
        const backFill = roomAvailable
          ? spaceId === item.id
            ? "rgba(77, 154, 191, 0.5"
            : "transparent"
          : "rgba(136, 152, 170, 0.85)";
        childItem
          .attr({ fill: backFill })
          .mouseover(function () {
            if (roomAvailable) {
              this.fill({ color: "rgba(0,0,0,0.5)" });
            }
          })
          .mouseout(function () {
            this.fill({ color: backFill });
          });
      }
    };

    if (draw && spaces) {
      drawRooms();
    }
  }, [
    draw,
    venue,
    wrapperId,
    id,
    spaceId,
    onChangeRoom,
    img,
    parentWidth,
    parentHeight,
    spaces,
  ]);

  return (
    <>
      {imgLoadError && (
        <div className="img-load-error">Failed to load room map image</div>
      )}
      <div id={id} ref={svgItem}></div>
    </>
  );
}

export default BookingVenue;
