import React from "react";
import ReactDom from "react-dom";
import { connect } from "react-redux";
import { Calendar } from "@fullcalendar/core";
import dayGridPlugin from "@fullcalendar/daygrid";
import timeGridPlugin from "@fullcalendar/timegrid";
import interaction from "@fullcalendar/interaction";
import { Card, CardBody, Container, Row, Col } from "reactstrap";
import { events } from "variables/general.jsx";
import Loading from "../../../components/Loading";
import Navigator from "./Navigator";
import BookingModal from "./BookingModal";
import moment from "moment";
import {
  initData,
  addNewBooking,
  clearState,
  editAppointmentModal,
  changeVenueForModal,
  cancelAddNewBooking,
  bookSpace,
  addAppointment,
  deleteAppointment,
  editAppointment,
  getDayBookings,
} from "../../../redux/actions/providerDashboardActions";

import VenueAndSpaceSelection from "./VenueAndSpaceSelection";
import CustomEvent from "./CustomEvent";
import EventDetail from "./EventDetail";

import "./Dashboard.scss";
import Button from "reactstrap/lib/Button";
let calendar;

class CalendarView extends React.Component {
  state = {
    events: events,
    canLookup: true,
  };

  calendar = null;

  componentDidMount() {
    const calendar = this.createCalendar();
    this.props.initData(null, calendar);
  }

  changeCalendar = () => {
    // let date = calendar.view.activeStart + "-" + calendar.view.activeEnd;
    let date =
      moment(calendar.view.activeStart).format("MMM DD, YYYY") +
      " - " +
      moment(calendar.view.activeEnd)
        .subtract(1, "days")
        .format("MMM DD, YYYY");
    this.setState({
      currentDate: date,
      canLookup: calendar.view.viewSpec.durationUnit !== "month",
    });
  };

  createCalendar = () => {
    calendar = new Calendar(this.refs.calendar, {
      plugins: [interaction, dayGridPlugin, timeGridPlugin],
      views: {
        month: {
          columnHeaderFormat: {
            weekday: "narrow",
          },
        },
        week: {
          columnHeaderFormat: {
            weekday: "narrow",
            day: "2-digit",
          },
        },
      },
      defaultView: "timeGridWeek",
      selectable: true,
      selectHelper: true,
      allDaySlot: false,
      // slotDuration: "00:30",
      eventOverlap: false,
      // editable: true,
      minTime: "06:00:00",
      maxTime: "21:30:00",
      eventLimit: true,
      select: (info) => {
        this.props.addNewBooking(info);
      },
      // Edit calendar event action
      eventClick: ({ event }) => {
        this.setState({
          modalChange: true,
          eventId: event.id,
          eventTitle: event.title,
          eventDescription: event.extendedProps.description,
          radios: "bg-info",
          event: event,
        });
        this.props.editAppointmentModal(event._def.extendedProps.data);
      },
      eventRender: function (info) {
        const type = info.event._def.extendedProps.type;
        if (type === "BOOKING") {
          ReactDom.render(
            <CustomEvent {...info}>
              <EventDetail {...info} />
            </CustomEvent>,
            info.el
          );
        }
      },
    });
    calendar.render();
    this.calendar = calendar;

    let date =
      moment(calendar.view.activeStart).format("MMM DD, YYYY") +
      " - " +
      moment(calendar.view.activeEnd)
        .subtract(1, "days")
        .format("MMM DD, YYYY");

    this.setState({
      currentDate: date,
    });

    return calendar;
  };

  componentWillUnmount() {
    this.props.clearState();
  }

  changeVenueForModal(venueId) {
    if (venueId) {
      this.props.changeVenueForModal(venueId);
    }
  }

  next = () => {
    this.calendar.next();

    this.changeCalendar();
  };

  prev = () => {
    this.calendar.prev();

    this.changeCalendar();
  };

  render() {
    const {
      loading,
      venues,
      spaces,
      selectedVenue,
      spacesForModal,
      bookSpace,
      cancelAddNewBooking,
      addAppointment,
      deleteAppointment,
      editAppointment,
      clients,
      dayBookings,
      getDayBookings,
      modalVenue,
    } = this.props;
    const spacesDisplay = spacesForModal || spaces;
    const { canLookup } = this.state;
    return (
      <div className="provider-dashboard">
        <div className="header header-dark bg-info pb-6 content__title content__title--calendar">
          <Container fluid>
            <div className="header-body" />
          </Container>
        </div>
        <Container className="mt--6" fluid>
          {loading && <Loading />}
          <Row>
            <Col className="order-2 order-md-1" xs={12}>
              <Row className="align-items-center py-4">
                <Col lg="6">
                  <Button
                    className="fullcalendar-btn-prev btn-neutral"
                    color="default"
                    onClick={this.prev}
                    size="sm"
                  >
                    <i className="fas fa-angle-left" />
                  </Button>

                  <h6 className="fullcalendar-title h2 d-inline-block mb-0 mr-1">
                    {this.state.currentDate}
                  </h6>
                  <Button
                    className="fullcalendar-btn-next btn-neutral"
                    color="default"
                    onClick={this.next}
                    size="sm"
                  >
                    <i className="fas fa-angle-right" />
                  </Button>
                </Col>
                <Col className="mt-3 mt-md-0 text-md-right" lg="6">
                  <Navigator
                    calendar={calendar}
                    onChange={this.changeCalendar}
                  />
                </Col>
              </Row>
            </Col>
            <Col className="order-3 order-md-2">
              <Card className="card-calendar">
                <CardBody className="p-0">
                  <div
                    className="calendar"
                    data-toggle="calendar"
                    id="calendar"
                    ref="calendar"
                  />
                </CardBody>
              </Card>
            </Col>
            <Col md={3} className="order-1 order-md-3 pl-md-0">
              <VenueAndSpaceSelection canLookup={canLookup} />
            </Col>
          </Row>
        </Container>
        <BookingModal
          venues={venues}
          spaces={spacesDisplay}
          selectedVenue={selectedVenue}
          getSpaces={(e) => this.changeVenueForModal(e)}
          clients={clients}
          modalVenue={modalVenue}
          loading={loading}
          bookSpace={bookSpace}
          addAppointment={addAppointment}
          deleteAppointment={deleteAppointment}
          cancelAddNewBooking={cancelAddNewBooking}
          editAppointment={editAppointment}
          dayBookings={dayBookings}
          getDayBookings={getDayBookings}
        />
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    loading: state.providerDashboard.loading,
    isFirstLogin: state.auth.isFirstLogin,
    venues: state.providerDashboard.venues,
    spaces: state.providerDashboard.spaces,
    modalVenue: state.providerDashboard.modalVenue,
    spacesForModal: state.providerDashboard.spacesForModal,
    selectedVenue: state.providerDashboard.selectedVenue,
    dayBookings: state.providerDashboard.dayBookings,
    clients: state.providerDashboard.clients,
  };
};

const mapDispatchToProps = {
  initData,
  addNewBooking,
  clearState,
  changeVenueForModal,
  editAppointmentModal,
  cancelAddNewBooking,
  bookSpace,
  addAppointment,
  deleteAppointment,
  editAppointment,
  getDayBookings,
};

export default connect(mapStateToProps, mapDispatchToProps)(CalendarView);
