import React, { useState } from 'react';
import classNames from 'classnames/bind'
import { Modal, ModalHeader, ModalBody, ModalFooter, Button } from 'reactstrap'

const BookingPolicy = (props) => {
    const { agree, onChange, className } = props

    const [isShowPolicy, setShowPolicy] = useState(false);

    const togglePolicy = () => {
        setShowPolicy(!isShowPolicy);
    }

    return <div className={classNames("policy custom-control custom-checkbox", className)}>
        <input
            className="custom-control-input"
            id="customCheck1"
            type="checkbox"
            checked={agree}
            onChange={() => onChange(!agree)}
        />
        <label
            className="custom-control-label"
            htmlFor="customCheck1">
            I have read and agree to the Ethera Booking Policy
        </label>
        <i className="far fa-question-circle c-pointer ml-2" onClick={togglePolicy}></i>
        <PolicyModal isOpen={isShowPolicy} toggle={togglePolicy} />
    </div>
}

const PolicyModal = (props) => {
    return <Modal {...props} size="lg">
        <ModalHeader>Booking policy</ModalHeader>
        <ModalBody>
            <p>Bookings can be cancelled within 24 hours without penalty.
            After 24 hours, any cancelled booking will be deducted from the monthly cancellation allowance.
            You will not be able to cancel a booking if you have exceeded the allowance of 10 cancellations a month.</p>
            <p>
                Please check our booking policies here:
                <br />
                <a href="https://www.myethera.com/membership-policies" target="blank">www.myethera.com/membership-policies</a>
            </p>

        </ModalBody>
        <ModalFooter>
            <Button color="primary" onClick={props.toggle}>Got it!</Button>{' '}
        </ModalFooter>
    </Modal>
}

export default BookingPolicy;