import React from "react";

import ReactDatetime from "react-datetime";

import SectionHeader from "../../../../components/Form/SectionHeader";

import { FormGroup, Row, Col, Input } from "reactstrap";

const BookingDatetime = (props) => {
  const {
    useDate = true,
    validDate,
    startDate,
    startTime,
    endTime,
    duration,

    onChangeDate,
    onChangeStart,
    onChangeEnd,
    onChangeDuration,
    onBlurDuration,
    step = 15,
  } = props;
  return (
    <div className="date-time">
      <SectionHeader icon="far fa-clock" title="Date & Time" />
      <Row>
        {useDate && (
          <>
            <Col md={6}>
              <FormGroup>
                <label
                  className="form-control-label"
                  htmlFor="bookingStartDate"
                >
                  Date
                </label>
                <ReactDatetime
                  id="bookingStartDate"
                  timeFormat={false}
                  closeOnSelect={true}
                  isValidDate={validDate}
                  value={startDate}
                  inputProps={{ readOnly: true }}
                  onChange={(date) => onChangeDate(date)}
                >
                  <span className="btn-inner--icon">
                    <i className="ni ni-atom" />
                  </span>
                </ReactDatetime>
              </FormGroup>
            </Col>
            <Col md={6}></Col>
          </>
        )}
        <Col md={3}>
          <FormGroup>
            <label className="form-control-label" htmlFor="bookingStartTime">
              Start time
            </label>
            <ReactDatetime
              id="bookingStartTime"
              value={startTime}
              timeFormat={true}
              closeOnSelect={true}
              dateFormat={false}
              timeConstraints={{ minutes: { step } }}
              inputProps={{ readOnly: true }}
              onChange={onChangeStart}
            />
          </FormGroup>
        </Col>
        {useDate && (
          <Col md={3}>
            <FormGroup>
              <label className="form-control-label" htmlFor="bookingDuration">
                Duration
              </label>
              <Input
                id="bookingDuration"
                type="number"
                step={step / 60}
                min="1"
                max="14"
                value={duration}
                onChange={onChangeDuration}
                onBlur={onBlurDuration}
              />
            </FormGroup>
          </Col>
        )}
        <Col md={3}>
          <FormGroup>
            <label className="form-control-label" htmlFor="bookingEndTime">
              End time
            </label>
            <ReactDatetime
              id="bookingEndTime"
              value={endTime}
              closeOnSelect={true}
              timeFormat={true}
              dateFormat={false}
              timeConstraints={{ minutes: { step } }}
              inputProps={{ readOnly: true }}
              onChange={onChangeEnd}
            />
          </FormGroup>
        </Col>
      </Row>
    </div>
  );
};

export default BookingDatetime;
