import React, { useReducer } from 'react';
import { useDispatch } from 'react-redux';
import { Button, Row, Col } from 'reactstrap';

import { getTimeFull } from '../../../../helpers/UtilsHelper'
import actions from '../../../../redux/actions/providerDashboardActions'

import ErrorSummary from '../../../../components/ErrorSummary';
import BookingService from "./BookingService"
import BookingDatetime from "./BookingDatetime"
import BookingClient, { NEW_CLIENT, OTHER_CLIENT } from "./BookingClient"
import AppointmentView from "./AppointmentView"
import { useState } from 'react';

const RESET = 'RESET';
const defaultState = (props) => {
    return {
        clientType: 1,
        clientId: '',
        firstName: '',
        lastName: '',
        phoneNumber: '',
        email: '',
        serviceType: '',
        servicePrice: 0.0,
        comment: '',
        startTime: props.minTime,
        endTime: props.maxTime,
        showAddNew: false,
    }
}

function clientReducer(state, action) {
    const [field, value] = action;
    if (field === RESET) {
        return defaultState(value);
    }

    return { ...state, [field]: value }
}

const AppointmentStep = (props) => {
    const { clients, appointments, addAppointment, bookingId, deleteAppointment, cancel, appointmentError } = props;
    const [state, changeState] = useReducer(clientReducer, defaultState(props));

    const {
        clientType,
        clientId,
        firstName,
        lastName,
        phoneNumber,
        email,
        serviceType,
        servicePrice,
        comment,
        startTime,
        endTime,
        isShowAddNew,
    } = state;

    const dispatch = useDispatch();

    const [editAppointmentId, setEditAppointmentId] = useState(null);

    const clearError = () => {
        dispatch({ type: actions.CLEAR_APPOINTMENt_ERROR })
    }

    const reset = () => {
        changeState([RESET, props])
    }

    const addNew = () => {
        const {
            clientType,
            firstName,
            lastName,
            email,
            phoneNumber,
            serviceType,
            servicePrice,
            comment,
            startTime,
            endTime,
            clientId
        } = state;
        let data = {
            bookingId: bookingId,
            startTime: getTimeFull(startTime),
            endTime: getTimeFull(endTime),
            serviceType,
            servicePrice,
            comment
        }

        switch (clientType) {
            case 1:
                data.client = clientId;
                break;
            case 2:
                data.newClient = {
                    firstName, lastName, email, phoneNumber
                }
                break;
            default:
                break;
        }

        addAppointment(data, reset)
    }

    const showAddNew = () => {
        changeState(["isShowAddNew", true])
        setEditAppointmentId(null);
        clearError();
    }

    const editAppointment = (appointmentId) => {
        setEditAppointmentId(appointmentId === editAppointmentId ? null : appointmentId);
        changeState(["isShowAddNew", false])
        clearError();
    }

    const performEdit = (data) => {
        props.editAppointment(data, () => setEditAppointmentId(null));
    }

    const appointmentList = appointments.slice(0);
    appointmentList.sort((a1, a2) => parseInt(a2.startTime.replace(/:/g, "")) - parseInt(a1.startTime.replace(/:/g, "")))

    const isUserValid = [OTHER_CLIENT, NEW_CLIENT].includes(clientType) || clientId !== ''

    return <div className="booking-from">
        {appointmentList.map(
            (ap, index) => <AppointmentView key={ap.id} editingAppointment={editAppointmentId} cancelEdit={() => editAppointment(null)} index={index} clients={clients} appointment={ap} onDelete={deleteAppointment} onEdit={(e) => editAppointment(e)} performEdit={(e) => performEdit(e)} />
        )}
        {isShowAddNew ?
            <>
                <BookingClient
                    clientType={clientType}
                    clients={clients}
                    clientId={clientId}
                    firstName={firstName}
                    lastName={lastName}
                    phoneNumber={phoneNumber}
                    email={email}
                    onChangeState={(field, value) => changeState([field, value])}
                />
                <BookingDatetime
                    useDate={false}
                    startTime={startTime}
                    endTime={endTime}

                    onChangeStart={(time) => changeState(["startTime", time])}
                    onChangeEnd={(time) => changeState(["endTime", time])}
                    step={5}
                />
                <BookingService
                    serviceType={serviceType}
                    servicePrice={servicePrice}
                    comment={comment}
                    onChangeState={(field, value) => changeState([field, value])}
                />

                {(clientType === NEW_CLIENT) && <p><b>New clients will automatically receive an invitation email to set up their Client Portal.</b></p>}
                <ErrorSummary error={appointmentError} className="mt-3" />
                <div className="d-flex justify-content-between mt-3">
                    <Row>
                        <Col className="text-center">
                            <Button color="link" className="pl-0" onClick={reset}>CANCEL ADD NEW CLIENT APPOINTMENT</Button>
                        </Col>
                        <Col className="text-center">
                            <Button color="success" disabled={!isUserValid} onClick={addNew}>Add</Button>
                        </Col>
                    </Row>
                </div>
            </> :
            // editAppointmentId === null &&
            <div className="d-flex justify-content-between">
                <Row>
                    <Col className="text-center">
                        <Button color="link" className="pl-0" onClick={showAddNew}>ADD NEW CLIENT APPOINTMENT</Button>
                    </Col>
                    <Col className="text-center">
                        <Button color="success" onClick={cancel}>COMPLETE</Button>
                    </Col>
                </Row>
            </div>
        }
    </div>
}

export default AppointmentStep;