import _ from "lodash";
import React from "react";
import { connect } from "react-redux";
import moment from "moment";
import { Modal, Button, Row, Col } from "reactstrap";

import {
  getUserFullName,
  getDate,
  getTimeFull,
  getDurationTime,
  getBookingDate,
} from "../../../../helpers/UtilsHelper";
import { getAvailableSpacesInRange } from "../../../../helpers/BookingHelper";
// import { cancelAddNewBooking, bookSpace, addAppointment, deleteAppointment } from '../../../../redux/actions/providerDashboardActions'
import Loading from "../../../../components/Loading";
import ErrorSummary from "../../../../components/ErrorSummary";
import { bookingSteps } from "../../../../constants";

import BookingPreview from "./BookingPreview";
import BookingDatetime from "./BookingDatetime";
import BookingVenue, {
  ETHERA_VENUE,
  NON_ETHERA,
  bookingRoomModes,
} from "./BookingVenue";
import AppointmentStep from "./AppointmentStep";
import BookingPolicy from "./BookingPolicy";

import "./BookingModal.scss";

const defaultState = () => {
  let today = moment().startOf("date");
  let tomorrow = today.clone().add(1, "d");

  return {
    clientType: 1,
    venueType: ETHERA_VENUE,
    startDate: tomorrow,
    startTime: moment("8:00:00", "hh:mm:ss"),
    endTime: moment("9:00:00", "hh:mm:ss"),
    duration: 0,
    unitPrice: 0,
    spaceId: "",
    errors: {},
    appointments: [],
    availableSpaces: [],
    appointmentStartTime: moment("8:00:00", "hh:mm:ss"),
    appointmentEndTime: moment("9:00:00", "hh:mm:ss"),
    agreePolicy: false,
    selectedVenue: null,
    mode: bookingRoomModes.LIST,
  };
};

class BookingModal extends React.Component {
  constructor(props) {
    super(props);
    this.state = defaultState();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.currentBooking && nextProps.currentBooking.space) {
      this.setState({
        spaceId: _.get(nextProps, "currentBooking.space.id", ""),
      });
    }
  }

  changeVenueType = (venueType) => {
    this.setState({ venueType });
  };

  validDate = (current) => {
    var yesterday = moment().subtract(1, "day");
    return current.isAfter(yesterday);
  };

  onChangeState = (name, value) => {
    try {
      this.setState({ [name]: value });
    } catch (error) {}
  };

  onChangeRoom = (value) => {
    if (value) {
      const selectedRoom = this.props.spaces.filter(
        (item) => item.id === value
      )[0];

      const { tier } = this.props;
      // Check have price, fix bug not prices
      let unitPrice = 0;
      if (selectedRoom.prices) {
        unitPrice = Number(
          selectedRoom.prices.find((p) => p.tier === tier).cost
        );
      }
      try {
        this.setState({ spaceId: value, unitPrice });
      } catch (error) {}
    }
  };

  onChangeLocation = (value) => {
    this.setState({ selectedVenue: value, spaceId: "" });
    this.props.getSpaces && this.props.getSpaces(value);
  };

  onChangeStart = (dateTime) => {
    let { startTime, duration } = this.state;
    let newEndTime = moment(dateTime).add(duration, "h");

    if (
      moment(dateTime).startOf("date").diff(moment(newEndTime).startOf("date"))
    ) {
      this.onChangeDateTime({ startTime });
      return;
    }

    this.onChangeDateTime({ startTime: dateTime, endTime: newEndTime });
  };

  onChangeEnd = (dateTime) => {
    let { startTime, endTime } = this.state;
    let duration = moment.duration(dateTime.diff(startTime)).asHours();

    if (duration < 1) {
      this.onChangeDateTime({ endTime });
      return;
    }

    if (
      moment(startTime).startOf("date").diff(moment(dateTime).startOf("date"))
    ) {
      this.onChangeDateTime({ endTime });
      return;
    }

    this.onChangeDateTime({ duration, endTime: dateTime });
  };

  onChangeDuration = (e) => {
    let duration = Number(e.target.value);

    let { startTime, endTime } = this.state;
    endTime = moment(startTime).add(duration, "h");
    if (
      moment(startTime).startOf("date").diff(moment(endTime).startOf("date"))
    ) {
      return;
    }

    this.onChangeDateTime({ endTime, duration });
  };

  onBlurDuration = (e) => {
    let duration = Number(e.target.value);

    if (duration < 1) {
      duration = 1;
    }

    if (duration > 14) {
      duration = 14;
    }

    // Only allow to input a
    if (duration % 0.25) {
      duration = parseInt(duration / 0.25) * 0.25;
    }

    let { startTime, endTime } = this.state;
    endTime = moment(startTime).add(duration, "h");
    if (
      moment(startTime).startOf("date").diff(moment(endTime).startOf("date"))
    ) {
      return;
    }

    this.onChangeDateTime({ endTime, duration });
  };

  onChangeDate = (value) => {
    this.props.getDayBookings(value);
    this.onChangeDateTime({ startDate: value });
  };

  onChangeDateTime = (data) => {
    const { venues, spaces, dayBookings, selectedVenue } = this.props;
    let newState = { ...this.state, ...data };
    let { startDate, startTime, endTime, spaceId } = newState;

    const venue = venues.find((venue) => venue.id === selectedVenue);
    const startDatetime = moment(
      `${getDate(startDate)}T${getTimeFull(startTime)}`
    );

    const endDatetime = moment(`${getDate(startDate)}T${getTimeFull(endTime)}`);
    const availableSpaces =
      getAvailableSpacesInRange(
        spaces,
        venue,
        dayBookings.bookings,
        startDatetime,
        endDatetime
      ) || [];
    if (!availableSpaces.some((item) => item.id === spaceId)) {
      spaceId = "";
    }

    newState.availableSpaces = availableSpaces;
    newState.spaceId = spaceId;
    this.setState(newState);
  };

  resetState = () => {
    this.setState(defaultState());
  };

  book = () => {
    const state = this.state;
    let data = {
      ...state,
      startDate: getDate(state.startDate),
      startTime: getTimeFull(state.startTime),
      endTime: getTimeFull(state.endTime),
      space: state.spaceId,
    };
    this.props.bookSpace(data, () => {});
  };

  cancel = () => {
    this.props.cancelAddNewBooking();
    this.resetState();
  };

  shouldComponentUpdate(nextProps, nextState) {
    if (!this.props.isShowAddBooking && nextProps.isShowAddBooking) {
      const { currentBooking } = nextProps;
      const startTime = moment(
        getBookingDate(currentBooking, currentBooking.startTime)
      );
      const endTime = moment(
        getBookingDate(currentBooking, currentBooking.endTime)
      );
      const duration = getDurationTime(startTime, endTime);
      this.props.getDayBookings(startTime);
      if (nextProps.selectedVenue) {
        this.props.getSpaces(nextProps.selectedVenue);
      }
      this.setState({
        startTime,
        endTime,
        duration,
        startDate: startTime,
        appointments: currentBooking.appointments || [],
        selectedVenue: this.state.selectedVenue || this.props.selectedVenue,
      });
    }
    return true;
  }

  componentDidMount() {}

  render() {
    const { onChangeState } = this;
    const {
      isShowAddBooking,
      clients,
      venues,
      spaces,
      loading,
      step,
      addAppointment,
      editAppointment,
      deleteAppointment,
      bookingError,
      appointmentError,
      displayVenues,
      currentBooking,
      dayBookings,
      modalVenue,
    } = this.props;

    const selectedVenue = this.state.selectedVenue;

    const appObj = {};

    if (currentBooking && currentBooking.appointments) {
      for (let app of currentBooking.appointments) {
        appObj[app.id] = app;
      }
    }

    const appointments = Object.values(appObj);
    let selectedBookingId = this.props.selectedBookingId;

    if (!selectedBookingId && this.props.currentBooking) {
      selectedBookingId = this.props.currentBooking.id;
    }

    let {
      startDate,
      duration,
      startTime,
      endTime,
      spaceId,
      unitPrice,
      venueType,
      agreePolicy,
      mode,
    } = this.state;

    spaces.forEach((space) => (space.text = space.name));

    const venue = venues.find((venue) => venue.id === selectedVenue);

    const startDatetime = moment(
      `${getDate(startDate)}T${getTimeFull(startTime)}`
    );
    const endDatetime = moment(`${getDate(startDate)}T${getTimeFull(endTime)}`);
    const availableSpaces = getAvailableSpacesInRange(
      spaces,
      venue,
      dayBookings.bookings,
      startDatetime,
      endDatetime
    );
    const totalServicePrice = appointments
      .map((a) => Number(a.servicePrice || 0))
      .reduce((a, b) => a + b, 0);

    const spaceOk = venueType === NON_ETHERA || !!spaceId;

    return (
      <Modal
        isOpen={isShowAddBooking}
        size="lg"
        fade={false}
        className="row booking-modal"
      >
        <Row className="mr-0 ml-0">
          <BookingPreview
            startDate={startDate}
            startTime={startTime}
            endTime={endTime}
            duration={duration}
            servicePrice={totalServicePrice}
            unitPrice={unitPrice}
            cancelAddNewBooking={this.cancel}
            getUserFullName={getUserFullName}
            spaceId={spaceId}
            venueId={selectedVenue}
            venueType={venueType}
            booking={currentBooking}
            appointments={appointments}
          />
          <Col md={9} className="detail">
            {loading && <Loading />}
            <div className="header">
              <h2>
                {step === bookingSteps.BOOKING
                  ? `Add New Booking`
                  : `Manage Appointments`}
              </h2>
            </div>
            {step === bookingSteps.BOOKING && (
              <div className="booking-from">
                <BookingDatetime
                  validDate={this.validDate}
                  startDate={startDate}
                  startTime={startTime}
                  endTime={endTime}
                  duration={duration}
                  onChangeDate={this.onChangeDate}
                  onChangeStart={this.onChangeStart}
                  onChangeDuration={this.onChangeDuration}
                  onBlurDuration={this.onBlurDuration}
                  onChangeEnd={this.onChangeEnd}
                  step={15}
                />
                <BookingVenue
                  key={availableSpaces.length}
                  venueType={venueType}
                  spaces={availableSpaces}
                  spaceId={spaceId}
                  changeVenueType={this.changeVenueType}
                  onChangeRoom={this.onChangeRoom}
                  onChangeLocation={this.onChangeLocation}
                  displayVenues={displayVenues}
                  venues={venues}
                  venueId={selectedVenue}
                  onChangeMode={(e) => this.setState({ mode: e })}
                  currentRoomMode={mode}
                  modalVenue={modalVenue}
                />
                <BookingPolicy
                  agree={agreePolicy}
                  onChange={(value) => onChangeState("agreePolicy", value)}
                />
                <ErrorSummary error={bookingError} className="mt-3" />
                <div className="d-flex justify-content-between mt-3">
                  <Button color="link" className="pl-0" onClick={this.cancel}>
                    CANCEL
                  </Button>
                  <Button
                    color="success"
                    disabled={!agreePolicy || !spaceOk || loading}
                    onClick={this.book}
                    title={spaceOk ? "" : "Please select a room"}
                  >
                    Agree
                  </Button>
                </div>
              </div>
            )}
            {step === bookingSteps.APPOINTMENT && (
              <AppointmentStep
                clients={clients}
                appointments={appointments}
                addAppointment={addAppointment}
                bookingId={currentBooking.id}
                minTime={startTime}
                maxTime={endTime}
                editAppointment={editAppointment}
                deleteAppointment={deleteAppointment}
                appointmentError={appointmentError}
                cancel={this.cancel}
              />
            )}
          </Col>
        </Row>
      </Modal>
    );
  }
}

const mapStateToProps = (state) => {
  const {
    isShowAddBooking,
    currentBooking,
    appointments,
    tier,
    bookings,
    selectedBookingId,
    step,
    bookingError,
    appointmentError,
  } = state.providerDashboard;

  return {
    isShowAddBooking,
    currentBooking,
    appointments,
    tier,
    bookings,
    selectedBookingId,
    step,
    bookingError,
    appointmentError,
  };
};

export default connect(mapStateToProps, null)(BookingModal);
