import React, { useReducer } from 'react';
import classNames from 'classnames/bind';
import _ from 'lodash';
import ReactDom from 'react-dom'
import {
    FormGroup,
    Row,
    Col,
    Button,
} from 'reactstrap';
import Select2 from 'react-select2-wrapper'

import { getUserFullName } from '../../../../helpers/UtilsHelper'
import SectionHeader from '../../../../components/Form/SectionHeader'
import ConfirmDeleteModal from '../../../../components/Modal/ConfirmDeleteModal'

import BookingService from "./BookingService"
import BookingDatetime from "./BookingDatetime"
import moment from 'moment';

function clientReducer(state, action) {
    const [field, value] = action;
    return { ...state, [field]: value }
}

const AppointmentView = (props) => {
    const { clients, appointment, onDelete, editingAppointment, cancelEdit, onEdit, performEdit } = props;

    const [state, changeState] = useReducer(clientReducer, {
        clientId: _.get(appointment, 'client.id', ''),
        serviceType: appointment.serviceType,
        servicePrice: appointment.servicePrice,
        comment: appointment.comment,
        startTime: moment(appointment.startTime, 'HH:mm:ss'),
        endTime: moment(appointment.endTime, 'HH:mm:ss'),
        isCollapse: true,
        showAddNew: false,
        showDeleteModal: false,
    });

    const {
        clientId,
        serviceType,
        servicePrice,
        comment,
        startTime,
        endTime,
        showDeleteModal,
    } = state;

    const deleteItem = () => {
        onDelete(appointment)
    }

    const edit = () => {
        const editedAppointment = Object.assign({}, state);
        editedAppointment.id = appointment.id;
        editedAppointment.bookingId = appointment.booking;
        editedAppointment.startTime = moment(editedAppointment.startTime).format("HH:mm:ss")
        editedAppointment.endTime = moment(editedAppointment.endTime).format("HH:mm:ss")
        performEdit(editedAppointment);
    }

    const client = clients.find(c => c.id === (appointment.client ? appointment.client.id : null));
    const displayName = client ? `${client.firstName} ${client.lastName}` : "Other appointment";
    const editing = editingAppointment === appointment.id;
    return <>
        <div className={classNames('appointment', !editing ? "section-collapse" : "")}>
            <div className="client mb-4">
                {/* <SectionHeader icon="far fa-user" title={"Client #" + (index + 1)}> */}
                <SectionHeader icon="far fa-user" title={`${displayName} ${moment(startTime).format("hh:mm A")}`}>
                    <Button color="link" className="p-0"
                        onClick={() => changeState(["showDeleteModal", true])}>Remove</Button>
                    <i className={classNames("ni c-pointer", !editing ? "ni-bold-down" : "ni-bold-up")}
                        onClick={() => onEdit(appointment.id)}></i>
                </SectionHeader>
                <Row className="info">
                    <Col md={8} className="mt-2">
                        <FormGroup className="select-user mb-0">
                            <label
                                className="form-control-label"
                                htmlFor="exampleDatepicker"
                            >Client name</label>
                            {clients.length > 0 &&
                                <Select2

                                    className="form-control"
                                    options={{
                                        placeholder: "Select user",
                                        minimumResultsForSearch: -1,
                                        templateSelection: formatSelectUser,
                                        templateResult: formatSelectUser,
                                        data: clients
                                    }}
                                    value={clientId}
                                    onChange={e => changeState(["clientId", e.target.value])}
                                />}
                        </FormGroup>
                    </Col>
                </Row>
            </div>
            <BookingDatetime
                useDate={false}
                startTime={startTime}
                endTime={endTime}

                onChangeStart={(time) => changeState(["startTime", time])}
                onChangeEnd={(time) => changeState(["endTime", time])}
            />
            <BookingService
                serviceType={serviceType}
                servicePrice={servicePrice}
                comment={comment}
                onChangeState={(field, value) => changeState([field, value])}
            />
            {editing && <div className="d-flex justify-content-between mt-3">
                <Row>
                    <Col className="text-center">
                        <Button color="link" className="pl-0" onClick={cancelEdit}>CANCEL EDIT CLIENT APPOINTMENT</Button>
                    </Col>
                    <Col className="text-center">
                        <Button color="success" onClick={() => edit()}>Save</Button>
                    </Col>
                </Row>
            </div>}
        </div>
        {showDeleteModal && <ConfirmDeleteModal
            className="modal-dialog-centered"
            isOpen={showDeleteModal}
            toggle={() => changeState(["showDeleteModal", !showDeleteModal])}
            handle={deleteItem}
            content={`Are you sure you want to permanently delete this appointment?`}
            showTitle={false}
        />}
    </>
}

const formatSelectUser = (user) => {
    const element = document.createElement('div')
    element.classList.add("select2-user")
    ReactDom.render(Select2User(user), element);
    return element;
}

function Select2User(user) {
    let label = user.id === "" ? user.text : getUserFullName(user)
    return <>
        <img
            alt="..."
            className="avatar avatar-sm rounded-circle mr-2"
            src={user.avatar ? user.avatar : require("assets/img/theme/default-avatar.png")}
        />
        <span>{label}</span>
    </>
}

export default AppointmentView;