import React from 'react';
import ReactDom from 'react-dom'
import { useSelector } from 'react-redux'
import _ from "lodash"
import {
    FormGroup,
    Button,
    ButtonGroup,
    Row,
    Col,
    Label
} from 'reactstrap';
import Select2 from 'react-select2-wrapper'

import { getUserFullName } from '../../../../helpers/UtilsHelper'
import { FormInput } from 'components/Form'
import SectionHeader from '../../../../components/Form/SectionHeader'
import './BookingClient.scss'

export const EXIST_CLIENT = 1
export const NEW_CLIENT = 2
export const OTHER_CLIENT = 3

const BookingClient = (props) => {
    const {
        clientType,
        clients,
        clientId,
        firstName,
        lastName,
        phoneNumber,
        email,

        // handleChange,
        onChangeState,
    } = props;

    const handleChange = e => {
        const { name, value } = e.target;
        onChangeState(name, value)
    }

    const appErrors = useSelector(state => state.providerDashboard.appointmentError)
    const errors = _.get(appErrors, "newClient", {})

    return <div className="client mb-4">
        <SectionHeader icon="fa fa-user-plus" title="Add Client" />
        <Row>
            <Col>
                <ButtonGroup className="mt-2">
                    <Button color="primary" onClick={() => onChangeState('clientType', EXIST_CLIENT)} active={clientType === EXIST_CLIENT}>
                        <i className="fa fa-circle"></i>Existing client
                    </Button>
                    <Button color="primary" onClick={() => onChangeState('clientType', NEW_CLIENT)} active={clientType === NEW_CLIENT}>
                        <i className="fa fa-circle"></i>New client
                    </Button>
                    <Button color="primary" onClick={() => onChangeState('clientType', OTHER_CLIENT)} active={clientType === OTHER_CLIENT}>
                        <i className="fa fa-circle"></i>Other
                    </Button>
                </ButtonGroup>
            </Col>
        </Row>
        {clientType === 1 &&
            <Row>
                <Col md={8} className="mt-2">
                    <FormGroup className="select-user mb-0">
                        <label
                            className="form-control-label"
                            htmlFor="exampleDatepicker"
                        >Client name</label>
                        {clients.length > 0 &&
                            <Select2
                                className="form-control"
                                options={{
                                    placeholder: "Select client",
                                    minimumResultsForSearch: -1,
                                    templateSelection: formatSelectUser,
                                    templateResult: formatSelectUser,
                                    data: clients
                                }}
                                value={clientId}
                                onChange={e => onChangeState("clientId", e.target.value)}
                            />}
                    </FormGroup>
                </Col>
            </Row>
        }
        {
            clientType === 2 &&
            <Row>
                <Col md={5} className="mt-3">
                    <FormGroup className="row mb-0">
                        <Label
                            className="form-control-label"
                            md="12"
                        >First Name</Label>
                        <Col md="12">
                            <FormInput required={true} type="text" name="firstName" value={firstName} errors={errors['firstName']} onChange={handleChange} />
                        </Col>
                    </FormGroup>
                </Col>
                <Col md={5} className="mt-3">
                    <FormGroup className="row mb-0">
                        <Label
                            className="form-control-label"
                            md="12"
                        >Last Name</Label>
                        <Col md="12">
                            <FormInput required={true} type="text" name="lastName" value={lastName} errors={errors['lastName']} onChange={handleChange} />
                        </Col>
                    </FormGroup>
                </Col>
                <Col md={5} className="mt-3">
                    <FormGroup className="row mb-0">
                        <Label
                            className="form-control-label"
                            md="12"
                        >Phone Number </Label>
                        <Col md="12">
                            <FormInput required={false} type="text" name="phoneNumber" value={phoneNumber} errors={errors['phoneNumber']} onChange={handleChange} regex="phoneNumber" />
                        </Col>
                    </FormGroup>
                </Col>
                <Col md={5} className="mt-3">
                    <FormGroup className="row mb-0">
                        <Label
                            className="form-control-label"
                            md="12"
                        > E-mail Address </Label>
                        <Col md="12">
                            <FormInput required={false} type="text" name="email" value={email} errors={errors['email']} onChange={handleChange} regex="email" />
                        </Col>
                    </FormGroup>
                </Col>
            </Row>
        }
    </div>
}

const formatSelectUser = (user) => {
    const element = document.createElement('div')
    element.classList.add("select2-user")
    ReactDom.render(Select2User(user), element);
    return element;
}

function Select2User(user) {
    let label = user.id === "" ? user.text : getUserFullName(user)
    return <>
        <img
            alt="..."
            className="avatar avatar-sm rounded-circle mr-2"
            src={user.avatar ? user.avatar : require("assets/img/theme/default-avatar.png")}
        />
        <span>{label}</span>
    </>
}

export default BookingClient;