import React, { useState } from "react";
import {
    Tooltip,
} from "reactstrap";
import moment from 'moment'
import { getUserFullName } from 'helpers/UtilsHelper'

const EventDetail = (props) => {
    const data = props.event.extendedProps.data
    const [tooltipOpen, setTooltipOpen] = useState(false);
    const toggle = () => setTooltipOpen(!tooltipOpen);
    return <Tooltip
        isOpen={tooltipOpen}
        className="event-tooltip"
        target={props.el}
        toggle={toggle}>
        {
            tooltipOpen && data.appointments.length > 0 ?
                data.appointments.slice(0, 3).map((app, index) => {
                    const clientName = getUserFullName(app.client, "Other appointment")
                    const start = moment(app.startTime, "HH:mm:ss").format("h:mm")
                    const end = moment(app.endTime, "HH:mm:ss").format("h:mm a")
                    return (
                        <div className={`${index > 0 ? "border-top" : ''}`} key={index}>
                            <h5 className={`text-center ${index > 0 ? "mt-2" : ''}`}>{clientName}</h5>
                            <h6 className="text-center">{start} - {end}</h6>
                        </div>
                    )
                }).concat(data.appointments.length <= 3 ? [] : [
                    <div className="border-top" key={4}>
                        <h5 className="text-center mt-2">{`(+ ${data.appointments.length - 3} more...)`}</h5>
                    </div>
                ])
                :
                <h5 className="text-center">No client appointments</h5>
        }
    </Tooltip>
}

export default EventDetail;
