import React from 'react';
import { getTime } from '../../../helpers/UtilsHelper';
import './CustomEvent.scss'

const CustomEvent = (props) => {
    const booking = props.event.extendedProps.data
    return (
        <>
            {
                booking && <div className="cursor-pointer provider-custom-event">
                    <div className={"ml-2 pt-1 font-weight-bold provider-custom-event__title" + (!booking.space ? " non-ethera" : "")}>{(booking.space && booking.space.name) || 'Non-Ethera'}</div>
                    <small className="m-2 mt-2">{getTime(`${booking.startDate} ${booking.startTime}`)} - {getTime(`${booking.startDate} ${booking.endTime}`)}</small>
                    {props.children}
                </div>
            }
        </>
    );
}

export default CustomEvent;