import React, { useEffect, useState } from 'react';
import spaceApi from '../../../../api/spaceApi'
import venueApi from '../../../../api/venueApi'

import {
  Col,
} from 'reactstrap';
import { NON_ETHERA } from './BookingVenue';

const BookingPreview = (props) => {
  const {
    startDate,
    startTime,
    endTime,
    duration,
    spaceId,
    venueId,
    venueType,
    appointments,
    unitPrice,
    cancelAddNewBooking,
    booking
  } = props;

  const [space, setSpace] = useState(null)
  const [venue, setVenue] = useState(null)

  useEffect(() => {
    if (!spaceId || spaceId === "") {
      return;
    }
    if (!space || space.id !== spaceId) {
      spaceApi.get(spaceId).then(data => {
        setSpace(data)
      })
    }

    if (venueId && (!venue || venue.id !== venueId)) {
      venueApi.detail(venueId).then(data => {
        setVenue(data)
      })
    }
  }, [spaceId, venueId])

  const isNonEtheraSpace = (venueType === NON_ETHERA || (booking.id !== undefined && space === null));
  const bookingTotal = booking.spacePrice !== undefined ? parseFloat(booking.spacePrice) : unitPrice * duration;
  const appointmentTotal = (appointments || []).reduce((acc, cur) => acc + Number(cur.servicePrice), 0)

  return <Col md={3} className="preview">
    <button type="button" className="close d-flex " aria-label="Close" onClick={cancelAddNewBooking}>
      <span aria-hidden="true">&times;</span>
    </button>
    <div className="time d-flex flex-column">
      <span className="font-weight-bold">{startDate.calendar().split(' at ')[0]}</span>
      <span className="font-weight-light">{startTime.format("h:mm a")} to {endTime.format("h:mm a")}</span>
    </div>
    <div className="duration d-flex flex-column mt-4">
      <span className="value">{parseFloat(duration).toFixed(2).replace(".00", "")}</span>
      <span className="label color-light">HOUR(S)</span>
    </div>
    {(space || isNonEtheraSpace) && <div className="space d-flex flex-column mt-4">
      <label className="label-highlight">{isNonEtheraSpace ? "Venue" : "Room"}</label>
      {space && !!space.images.length && <img alt="..." src={space.images[0].file} />}
      {!isNonEtheraSpace && <span className="mt-1">{space && space.name}</span>}
      {<span className="color-light">{!isNonEtheraSpace ? venue && venue.name : "Other"}</span>}
    </div>}
    <>
      {!isNonEtheraSpace &&
        <div className="space-price d-flex flex-column mt-4">
          <label className="label-highlight mb-0">Booking total</label>
          <div className="money">
            <span className="color-light">$</span>
            <span className="value">{bookingTotal}</span>
          </div>
        </div>
      }
      <div className="space-price d-flex flex-column mt-4">
        <label className="label-highlight mb-0">Appointment total</label>
        <div className="money">
          <span className="color-light">$</span>
          <span className="value">{appointmentTotal}</span>
        </div>
      </div>
    </>
  </Col>
}

export default BookingPreview;