import React from 'react';

import SectionHeader from '../../../../components/Form/SectionHeader'

import {
    FormGroup,
    Row,
    Col,
    Input,
} from 'reactstrap'

const BookingService = (props) => {
    const {
        serviceType,
        servicePrice,
        comment,
        onChangeState,
    } = props

    return <div className="service">
        <SectionHeader icon="fa fa-inbox" title="Service" />
        <Row>
            <Col md={6} className="mt-2">
                <FormGroup>
                    <label
                        className="form-control-label"
                        htmlFor="exampleDatepicker"
                    >Service type</label>
                    <Input type="text"
                        value={serviceType}
                        onChange={e => onChangeState("serviceType", e.target.value)} />
                </FormGroup>
            </Col>
            <Col md={4} className="mt-2">
                <FormGroup>
                    <label
                        className="form-control-label"
                        htmlFor="exampleDatepicker"
                    >Price</label>
                    <span className="title-note">(per session)</span>
                    <Input type="number"
                        value={servicePrice}
                        onChange={e => onChangeState("servicePrice", e.target.value)}
                    />
                </FormGroup>
            </Col>
        </Row>
        <Row>
            <Col className="mt-2">
                <FormGroup>
                    <label
                        className="form-control-label"
                        htmlFor="exampleDatepicker"
                    >Comment</label>
                    <Input type="textarea"
                        value={comment || ""}
                        onChange={e => onChangeState("comment", e.target.value)}
                    />
                </FormGroup>
            </Col>
        </Row>
    </div>
}

export default BookingService;