import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Calendar } from "@fullcalendar/core";

import { Button } from "reactstrap";

import { onCalendarChange } from "../../../redux/actions/providerDashboardActions";
import constants from "../../../constants";
class Navigator extends React.Component {
  next = () => {
    const { calendar } = this.props;
    calendar.next();
    this.props.onCalendarChange();
    this.props.onChange();
  };

  prev = () => {
    this.props.calendar.prev();
    this.props.onCalendarChange();
    this.props.onChange();
  };

  changeView = (newView) => {
    const { calendar } = this.props;
    calendar.changeView(newView);
    this.props.onCalendarChange();
    this.props.onChange();
  };

  render() {
    return (
      <>
        {/* <Button
          className="fullcalendar-btn-prev btn-neutral"
          color="default"
          onClick={this.prev}
          size="sm"
        >
          <i className="fas fa-angle-left" />
        </Button>
        <Button
          className="fullcalendar-btn-next btn-neutral"
          color="default"
          onClick={this.next}
          size="sm"
        >
          <i className="fas fa-angle-right" />
        </Button> */}
        <Button
          className="btn-neutral"
          color="default"
          data-calendar-view="month"
          onClick={() => this.changeView(constants.CALENDAR_VIEW_TYPE.MONTH)}
          size="sm"
        >
          Month
        </Button>
        <Button
          className="btn-neutral"
          color="default"
          data-calendar-view="basicWeek"
          onClick={() => this.changeView(constants.CALENDAR_VIEW_TYPE.WEEK)}
          size="sm"
        >
          Week
        </Button>
        <Button
          className="btn-neutral"
          color="default"
          data-calendar-view="basicDay"
          onClick={() => this.changeView(constants.CALENDAR_VIEW_TYPE.TIME_DAY)}
          size="sm"
        >
          Day
        </Button>
      </>
    );
  }
}

Navigator.protoTypes = {
  calendar: PropTypes.instanceOf(Calendar),
};

const mapStateToProps = (state) => {
  return {
    bookings: state.providerDashboard.calendar,
  };
};

const mapDispatchToProps = { onCalendarChange };

export default connect(mapStateToProps, mapDispatchToProps)(Navigator);
