import React, { useState } from "react";
import { connect, useDispatch, useSelector } from "react-redux";
import Select2 from 'react-select2-wrapper'
import {
    Button,
    Card,
    Input,
} from "reactstrap";

import actions from '../../../redux/actions/providerDashboardActions'
import SectionHeader from '../../../components/Form/SectionHeader'

import {
    changeVenue,
    changeSpaces,
    lookupAvailableSlot,
    clearLookupAvailableSlot
} from '../../../redux/actions/providerDashboardActions'

function VenueAndSpaceSelection(props) {
    const {
        //state
        venues,
        spaces,
        selectedVenue,
        selectedSpaces,

        //actions
        changeVenue,
        changeSpaces,
        lookupAvailableSlot,
        clearLookupAvailableSlot,
        canLookup,
    } = props

    const locationData = venues.map(venue => ({ ...venue, text: venue.name }));
    const [duration, setDuration] = useState(1);
    const isShowAll = useSelector(state => state.providerDashboard.isSelectAllRoom);
    const searchedSlots = useSelector(state => state.providerDashboard.searchedSlots);
    const dispatch = useDispatch();

    const lookup = () => {
        lookupAvailableSlot(duration * 60 * 60 * 1000);
    }

    const toggleAllRoom = () => dispatch({ type: actions.TOGGLE_ALL_ROOM })

    return <Card className="spaces">
        <div>
            <SectionHeader icon="ni ni-building" title="Location" />
            <div className="mb-3">
                <Select2
                    className="form-control"
                    defaultValue={selectedVenue}
                    onChange={state => changeVenue(state.target.value)}
                    options={{
                        placeholder: "Select venue",
                        templateResult: state => state.name,
                        templateSelection: state => state.name,
                        data: locationData
                    }}
                />
            </div>
        </div>
        <div>
            <SectionHeader icon="ni ni-collection" title="Rooms">
                <Button color="link" onClick={toggleAllRoom}>{isShowAll ? "Clear all" : "Select all"}</Button>
            </SectionHeader>
            <div className="spaces-select">
                <SpaceSelection
                    spaces={spaces}
                    selected={selectedSpaces}
                    onChange={changeSpaces}
                />
            </div>
        </div>
        <div className="text-center mt-2">
            <SectionHeader icon="ni ni-time-alarm" title="Duration" />
            <div>
                <Input
                    type="number"
                    value={duration}
                    min={1}
                    step={0.5}
                    onChange={e => setDuration(e.target.value)} />
                <div className="mt-2">
                    <Button color="variant" onClick={lookup} disabled={!canLookup}>Search</Button><br />
                    {searchedSlots && <Button color="link" onClick={clearLookupAvailableSlot}>Clear</Button>}
                </div>
            </div>
        </div>
    </Card>
}

const mapStateToProps = state => {
    const {
        venues,
        spaces,
        selectedVenue,
        selectedSpaces
    } = state.providerDashboard

    return {
        venues,
        spaces,
        selectedVenue,
        selectedSpaces
    }
}

const mapDispatchToProps = { changeVenue, changeSpaces, lookupAvailableSlot, clearLookupAvailableSlot }

export default connect(mapStateToProps, mapDispatchToProps)(React.memo(VenueAndSpaceSelection));

function SpaceSelection(props) {
    const { spaces, onChange, selected } = props

    const toggle = (id) => {
        const index = selected.indexOf(id);
        if (index >= 0) {
            selected.splice(index, 1)
        }
        else {
            selected.push(id)
        }

        const newSelected = [...selected]

        if (onChange) {
            onChange(newSelected)
        }
    }

    return <div className="select-space d-flex flex-column">
        {
            spaces.map(space => (
                <Button
                    active={selected.includes(space.id)}
                    key={space.id}
                    className="mb-2"
                    color="primary"
                    onClick={() => toggle(space.id)}
                    title={space.name}
                >
                    {space.name}
                </Button>
            ))
        }
    </div>
}